import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import hUpload from '@/components/h-upload/h-upload.vue';
export default {
	components: {
		uniNavBar,
		hUpload
	},
	data() {
		return {
			type1: [],
			typeNum1:0,
			type2: [],
			typeNum2:0,
			type2Index: 0,
			dates: '',
			upData: {
				type: '0',
				category: '0',
				time: '点击选择',
				address: '选择地点',
				longitude: '',
				latitude: '',
				content: '',
				imgList: '',
				videoSrc:""
			},
			imgList: [],
			videoList:[],
			typeList:[],
			childList:[],
			disabled:true
		};
	},
	onReady() {
		//获取上报事件
		this.getType();
		this.dates = this.getNowFormatDate()
	},

	methods: {
		tosts(){
			if(this.type2.length<=1){
				uni.showToast({
				    title: '请先选择类型',
				    duration: 2000,
					icon:"none"
				});	
				return
			}
		},
		 // 获取当前系统时间戳
		  getNowFormatDate() {
		      var date = new Date();
		      var seperator1 = "-";
		      var seperator2 = ":";
		      var month = date.getMonth() + 1;
		      var strDate = date.getDate();
		      if (month >= 1 && month <= 9) {
		          month = "0" + month;
		      }
		      if (strDate >= 0 && strDate <= 9) {
		          strDate = "0" + strDate;
		      }
		      var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
		              + " " + date.getHours() + seperator2 + date.getMinutes()
		              + seperator2 + date.getSeconds();
		      return currentdate;
		  },
		
		schange(val) {
			console.log(111);
			console.log(val);
		},
		setAttachData(val) {
			console.log(222);
			console.log(val);
			
			this.imgList = val.img;
			this.videoList=val.video
			console.log(this.imgList);
		},
		updataJump() {
			console.log('上报提交');
			//console.log(this.upData);
			
			console.log(this.upData);
			this.upData.imgList=this.imgList
			this.upData.videoSrc=this.videoList
			console.log(this.upData);
			if(this.upData.type=='0'){
				uni.showModal({
					content: "请选择类型",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.upData.category=='0'){
				uni.showModal({
					content: "请选择类别",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.upData.time=='点击选择'){
				uni.showModal({
					content: "请选择上报时间",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.upData.address=='选择地点'){
				uni.showModal({
					content: "请选择事发地点",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.upData.content==''){
				uni.showModal({
					content: "请输入事件描述",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.upData.imgList.length==0&&this.upData.videoSrc.length==0){
				uni.showModal({
					content: "请上传图片/视频",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			
			uni.showLoading({
				title: 'loading'
			});
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-grzx-wysb.htm', this.upData, { params: {} })
				.then(res => {
					uni.hideLoading();
					console.log('上报结果++++++');
					console.log(res);
					if (res.data.msgState == 1) {
						this.cleanData();
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
						
					}else{
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:"none"
						});
					}
					
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
			// setTimeout(() => {
			// 	uni.hideLoading();
			// 	uni.showToast({
			// 	    title: '上报成功',
			// 	    duration: 2000
			// 	});
			// 	this.cleanData();
			// }, 1000);
		},
		ontrueGetList() {
			
			
			this.dates = this.getNowFormatDate()
			//获取上报类型
			//this.getType1()
		},
		getType() {
			this.$http
				.get('/interface/rest/http/condition/xltz-sjlxlb.htm', { params: {} })
				.then(res => {
			//console.log('事件类型');
				//	console.log(res);
					if (res.data.msgState == 1) {
						var obj1 = [{ id: '0', name: '点击选择' }];
						var data = res.data.result;
						this.typeList= [...obj1, ...data];
						this.type1 = this.typeChange(this.typeList);
					
						this.type2=	this.typeChange(obj1)
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:"none"
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		getType1() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjlbxz.htm', { params: {} })
				.then(res => {
					console.log('事件类别');
					console.log(res);
					if (res.data.msgState == 1) {
							var obj1=[{id:'0',name:"点击选择"}]
							var data=res.data.list
							var obj=[...obj1,...data]
						this.type2=	this.typeChange(obj)
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:"none"
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		//处理函数
		typeChange(val) {
			var arr = [];
			for (let item in val) {
				//console.log(val[item].name);
				arr.push(val[item].name);
			}

			return arr;
		},
		cleanData() {
			this.$refs.upload.cleanAll();
			var upData = {
				type: '0',
				category: '0',
				time: '点击选择',

				address: '选择地点',
				longitude: '',
				latitude: '',

				content: '',
				imgList: ""
			};
			this.typeNum1=0
			this.typeNum2=0
			this.imgList=[]
			this.upData = upData;
		},
		sizeTypeChange(e) {
			
			var num=parseInt(e.detail.value)
			this.typeNum1=num
			this.upData.type =this.typeList[num].id ;
			var dataa=this.typeList[num].child
			
			var obj1=[{id:'0',name:"点击选择"}]
			this.childList=[...obj1,...dataa]
			this.type2=	this.typeChange(this.childList)
			this.disabled=false
			//console.log(this.type2)
			
		},
		sizeType2Change(e) {
			var num1=parseInt(e.detail.value)
			this.typeNum2=num1
			this.upData.category = this.childList[num1].id;
			//console.log(this.upData.type)
			//console.log(this.upData.category)
		},
		get_location() {
			const that = this;
			uni.showLoading({
				title: '加载中',
				mask: true
			});


			uni.getSetting({
				success(res) {
					uni.hideLoading();
					if (!res.authSetting['scope.userLocation']) {
						uni.authorize({
							scope: 'scope.userLocation',
							success() {
								that.chooseLocation();
								return;
							},

							fail() {
								uni.showToast({
									title: '无法获取地图权限',
									duration: 2000,
									icon:"none"
								});
							}
						});
					} else {
						that.chooseLocation();
						return;
					}
				}
			});

			//that.chooseLocation()
			//uni.hideLoading()
		},
		chooseLocation: function() {
			uni.chooseLocation({
				success: res => {
					console.log(res.name);
					console.log(res.address);
					console.log(res.longitude);
					console.log(res.latitude);
					// this.location = formatLocation(res.longitude, res.latitude),
					this.upData.address= res.name;
					this.upData.longitude = res.longitude;
					this.upData.latitude = res.latitude;
				}
			});
		},
		showPicker() {
			this.$refs.date1.show();
		},
		onConfirm(res, type) {
			this.upData.time = res.result;
			console.log(res.result);
		},
		//上传图片/视频
		upLoadImg() {
			console.log('根据选择调用不同的api');
		}
	}
};
